<template>
  <div class="row" style="margin-bottom: 30px">
    <p style="text-align: center">
      {{ $t("paymentSystemNotImplemented") }}
    </p>
  </div>
  <div class="row">
    <div class="col-6">
      <p style="text-align: center">
        <img style="width: 200px" :src="require('../assets/bookstree.png')" />
      </p>
    </div>
    <div class="col-6">
      <table
        style="width: 80%; text-align: center !important"
        class="table table-striped table-dark"
      >
        <thead class="thead-dark">
          <tr>
            <th scope="col">{{ $t("orderNr") }}</th>

            <th scope="col"></th>
            <th scope="col">{{ $t("price") }}</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{ orderDetails._id }}</td>

            <td></td>
            <td style="color: red">{{ orderDetails.orderPrice }} €</td>
          </tr>
        </tbody>
        <tr>
          <td></td>
          <td style="background: #fff !important; color: #000">{{ $t("discount") }}</td>
          <td style="background: #fff !important; color: green">
            -{{ orderDetails.orderDiscount }} €
          </td>
        </tr>
        <tr>
          <td></td>
          <td style="background: #fff !important; color: #000">{{ $t("priceSum") }}</td>
          <td style="background: #fff !important; color: green">0.00€</td>
        </tr>
        <tr>
          <td colspan="2"></td>
          <td>
            <button
              style="margin-top: 35px"
              type="button"
              class="btn btn-primary"
              @click.prevent="toMyOrders()"
            >
              {{ $t("toMyOrders") }}
            </button>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import OrdersService from "../services/OrdersService";
import UsersService from "../services/UsersService";

export default {
  name: "Payment",
  data() {
    return {
      userID: "",
      orderDetails: null,
    };
  },
  async beforeCreate() {
    this.userID = UsersService.getUserIDFromLocalStorage();
    try {
      this.orderDetails = await OrdersService.getUserLastOrder(this.userID);
      console.log(this.orderDetails);
    } catch (err) {
      console.log(err);
    }
  },
  methods: {
    toMyOrders() {
      this.$router.push(`/orders`);
    },
  },
};
</script>
