import axios from 'axios';

const url = '/api/orders/';

class BooksService {
    static postOrder(buyerID,boughtItems,orderPrice, orderDiscount) {
        var date = new Date();
        return new Promise((resolve, reject) => {
            try {
                let res= axios.post(url + "order",{
                    buyerID,
                    boughtItems,
                    orderPrice,
                    orderDiscount,
                    "date": date.toLocaleString()
                });
                resolve(res)
            } catch(err) {
                reject (err);
            }
        })
    }

    static getUserLastOrder(userID) {
        return new Promise((resolve, reject) => {
            axios.get(url + "last/" + userID).then(function(res) {
                    const resData = res.data;
                    resolve(resData);
                }).catch(function (error) {
                    reject(error)
                })
        })
    }

    static getUserOrders(userID) {
        return new Promise((resolve, reject) => {
            axios.get(url +"/"+ userID).then(function(res) {
                    const resData = res.data;
                    resolve(resData);
                }).catch(function (error) {
                    reject(error)
                })
        })
    }
}


export default BooksService;